*{margin: 0;padding: 0;}

.Header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
    padding: 16px 0;
    margin-bottom: 20px;
    border-bottom: 4px solid #3f51b5;
	width: 100%}

.Header__logo {
	margin-left: 15px;
	}

.Navigation {display: -ms-flexbox;display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    list-style: none; 
}

.Navigation__item {
    margin-left: 16px;
    font-family: Roboto,sans-serif;
}

.Navigation__link {
	margin-top: 6px;
	text-decoration: none;
	margin-right: 15px; 
	position: relative;
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: #424242;
    font-size: 22px;
 }
.Navigation__link:hover {
    color: #ff5722;}

.Navigation__link:after{
	position:absolute;
	top:47px;
	left:0;
	content:"";
	width:0px;
	height:4px;
	background:#ff5722;
	-webkit-transition:width .4s;
	-o-transition:width .4s;
	transition:width .4s}

.Navigation__link:hover:after{
	width:100%
}




